import f from "@/application/functions";
import InputField from './InputField/InputField.vue'
import _ from "underscore";
import { mapState } from "vuex";
import * as miscreant from "miscreant";

var enc = 'm.olm.v1.curve25519-aes-sha2'

export default {
	name: 'chatInput',
	props: {
		chat: Object,
		u: String
	},


	components: { InputField },

	data: function () {

		return {
			image_file: {},
			encrypted: false,
			upload: true,
			test: [],
			loading: false,
			text: '',
			file: {},
			fileInfo: {},
			ready: false,
			needencrypt: false,
			creating: false,
			userId: '',
			menuItems: [
				{
					click: "cameraHandler",
					title: "Take photo or Video",
					icon: "fas fa-camera",

					upload: {
						multiple: true,
						extensions: ['jpg', 'jpeg', 'png'],
						images: {
							resize: {
								type: 'fit'
							}
						}
					}
				},

				{
					click: "fileHandler",
					title: "Send File",
					icon: "fas fa-sticky-note",

					upload: {
						multiple: true,
						images: {
							resize: {
								type: 'fit'
							}
						}
					},
				}
			],
			anyUrlMeta: String,
			joinedMembers: []
		}

	},


	watch: {
		usersForKeysHash: {
			//immediate: true,
			handler: function () {

				console.log("downloadKeysHANDLER")
			}
		}
	},


	computed: {
		...mapState([
			'chats'
		]),

		transaction : function(){
			return null
			return f.deep(window, 'POCKETNETINSTANCE.platform.ui.wallet.send')
		},

		uusers: function () {
			if (this.u) {
				return _.map(this.u.split(','), (u) => {
					return u
				})
			}

			return []
		},
		ausers: function () {
			if (this.u) {
				return _.map(this.u.split(','), (u) => {
					return this.core.user.matrixId(u)
				})
			}

			return []
		},
		stateChat: function () {
			var id = this.$route.query.id
			return this.$store.state.chatsMap[id]
		},
		invited: function () {

			if (!this.chat) {

				if (this.u) return this.ausers

				return []

			}
			this.encrypted = this.core.mtrx.client.isRoomEncrypted(this.chat.roomId)
			return _.map(_.filter(this.chat.currentState.getMembers(), function (m, v) {
				return m.membership === 'invite'
			}), function (u) {
				return u.userId
			})
		},

		joined: function () {

			if (!this.chat) return []

			let roomId = this.chat.roomId
			let self = this
			let arr = []
			let members = 0

			_.each(this.chats, function (chat) {

				if (chat.roomId === roomId) {

					chat.members.forEach(function (user) {
						if (user.membership === 'join') {
							arr.push(user.userId)
						}
					})
				}
			})
			return arr
		},


	},

	created() {

	},

	///

	mounted() {
		console.log(this.core.mtrx.ready, "readyyyy")
		this.ready = true

		if (!this.chat) {
			this.newchat()
		}
	},

	methods: {
		wait: function () {
			return this.$f.pretry(() => {
				return this.core.mtrx.client && this.core.mtrx.access
			})
		},

		sendtransaction : function(){
			var api = this.transaction

			//TODO get address and send transaction

			api({
				roomid : this.chat.roomId,
				address : ''
			})
			
			/*.then(({txid, from}) => {

				return this.core.mtrx.transaction(this.chat.roomId, txid)

			})*/
			
		},

		emitInputData: function () {
			this.$emit('emptyInput')
			this.upload = true
		},

		HideUploadPic() {
			this.upload = false
		},

		emitUrl: function (url) {
			this.$emit('setMetaUrl', url)
		},

		async newchat() {
			if (this.u) {
				this.$store.state.globalpreloader = true
				var matrixId = null
				var myMatrixId = null
				var chat = null
				var id = ''

				this.creating = true
				this.core.user.usersInfo(this.uusers).then(info => {

					id = this.core.mtrx.kit.tetatetid(info[0], this.core.user.userinfo)

					matrixId = this.core.user.matrixId(info[0].id)
					myMatrixId = this.core.user.matrixId(this.core.user.userinfo.id)

					var initialstate = [{
						"type": "m.set.encrypted",
						"state_key": "",
						"content": {
							encrypted: true
						}
					}]

					return this.core.mtrx.client.createRoom(
						{
							room_alias_name: id,
							visibility: 'private',
							invite: [matrixId],
							name: '#' + id,
							initial_state: initialstate

							/*[
							  {
								"type": "m.room.guest_access",
								"state_key": "",
								"content": {
								  "guest_access": "can_join"
								}
							  },
			  
							]*/
						}
					)

				}).then(_chat => {

					chat = _chat
					this.$store.state.globalpreloader = false

	
					this.$emit("newchat", chat)
					let m_chat = this.core.mtrx.client.getRoom(_chat.room_id)
					let event = m_chat.currentState.getStateEvents("m.room.power_levels")
					return this.core.mtrx.client.setPowerLevel(chat.room_id, matrixId, 100, event[0]).then(r => {

					})

				}).then(r => {
					this.creating = false

				}).catch(e => {

					this.creating = false

					this.$store.state.globalpreloader = false

				})

			}

		},

		maySendMessage() {
			return this.chat && this.chat.maySendMessage()
		},

		greetings() {
			this.send('👋').then(r => {
				return Promise.resolve(r)
			})
		},

		sendinput(text, chatinput) {
			this.send(text.textContent).then(r => {
				return Promise.resolve(r)
			})
		},
		textCutLimit: function (text, limit) {
			text = text.trim();
			if (text.length <= limit) return text;

			text = text.slice(0, limit);

			return text.trim() + "...";
		},
		send(text, chatinput) {

			let self = this

			var t = this.textCutLimit(text, 1000)/*.replace(/\n/g, '<br/>')*/

			if (!this.chat) {
				this.newchat()
			}

			this.$emit("sending")
			this.$refs['newinput'].focus()

			return this.$f.pretry(() => {

				return this.chat && !this.creating

			}).then((r) => {

				this.$emit('sent')
				if (this.chat.pcrypto.canBeEncrypt()) {

					return this.chat.pcrypto.encryptEvent(text).then(r => {
						console.log('r', r)
						////

						
						return this.core.mtrx.client.sendEvent(this.chat.roomId, 'm.room.message', r)

					}).catch(e => {
						console.error("E", e)
					})

				}
				
				return this.core.mtrx.client.sendMessage(this.chat.roomId, {
					body: t,
					msgtype: 'm.text'
				})

			}).catch(e => {})

		},
		encryptMove: async function (text) {
			let keyData = new Uint8Array(32);

			window.crypto.getRandomValues(keyData);

			let key = await miscreant.SIV.importKey(keyData, "AES-SIV");
			var uint8array = new TextEncoder().encode(text);

			let plaintext = new Uint8Array(uint8array);
			let nonce = new Uint8Array(32);

			window.crypto.getRandomValues(nonce);

			let ciphertext = await key.seal(plaintext, nonce);

			let encryptObject = {
				body: f._arrayBufferToBase64(ciphertext.buffer),
				nonce: f._arrayBufferToBase64(nonce.buffer),
				key: f._arrayBufferToBase64(keyData.buffer),
				block: '1',
				msgtype: 'm.encrypted'
			}

			return this.core.mtrx.client.sendEvent(this.chat.roomId, 'm.room.message', encryptObject)
		},
		encrypt: function () {

			return this.core.mtrx.client.setRoomEncryption(this.chat.roomId, {
				algorithm: enc
			})

		},
		encryptIfNeed: function () {

			return Promise.resolve()

			var encrypted = this.core.mtrx.client.isRoomEncrypted(this.chat.roomId)

			if (!encrypted && _.toArray(this.chat.currentState.members).length == 2 && this.needencrypt) {
				this.needencrypt = false
				return this.encrypt()
			}

			return Promise.resolve()
		},

		pasteImage(data) {

			fetch(data).then(res => res.blob()).then(blob => {
				return new File([blob], "File name", { type: "image/png" })
			})
			.then(res => {
				this.image_file = res

				this.$emit('previewImg', data)

				return this.imageWH({
					base64 : data
				}).then(info => {

					console.log('info')

					return this.sendImage({ base64: data, file: res }, info)
				})
				
			})
			.catch(error => {
				console.log("error", error)
				return error
			})

		},

		sendImage: function (data, info) {
			this.$emit("sending")

			this.$f.pretry(() => {

				return this.chat

			}).then(() => {

				return this.core.mtrx.uploadContent(data.file)

			}).then((image) => {

				this.core.mtrx.client.sendImageMessage(this.chat.roomId, image, info, 'Image')
				this.$emit('previewImg', {})

				return this.encryptIfNeed()

			}).catch(e => {
			})
		},

		sendFile: function (data) {
			this.file = data.file

			this.$emit("sending")

			this.$f.pretry(() => {

				return this.chat

			}).then(() => {

				return this.core.mtrx.client.uploadContent(this.file)

			}).then(src => {
				return Promise.resolve(this.core.mtrx.client.mxcUrlToHttp(src))

			}).then((url) => {

				this.fileInfo.url = url
				this.fileInfo.name = this.file.name
				this.fileInfo.size = this.file.size

				let body = JSON.stringify(this.fileInfo)

				this.core.mtrx.client.sendMessage(this.chat.roomId, {
					body: body,
					msgtype: 'm.file'
				})

				this.$emit('anyFileSend', {})
				this.$emit('sent')

				this.file = {}

				return this.encryptIfNeed()

			}).catch(e => {
			})
		},
		focus: function () {

		},

		blur: function () {
		},

		change: function () {
		},
		keyup: function (evt) {
			var value = evt.target.value

			if (value === '') {
				this.$emit('inputClean', false)
				return
			} else {
				this.$emit('inputClean', true)
			}

			this.text = value
			this.anyUrlMeta = f.getUrl(this.text)


			if (this.anyUrlMeta !== undefined) {
				this.$emit('setMetaUrl', this.anyUrlMeta)
			} else {
				this.$emit('inputClean', false)
			}

			if (this.chat)
				this.core.mtrx.client.sendTyping(this.chat.roomId, true, 100)
		},
		menuItemClickHandler(item, rowObject) {
			debugger
			this[item.click](rowObject);
		},
		menuItemLoadedHandler: function (value) {

			this.menuIsVisible = value

			return this.menuIsVisible
		},

		uploadStart(item, files) {

		},

		uploadError(item, error) {
			console.log('error', error)
			this.$store.commit('icon', {
				icon: 'error',
				message: error.text
			})
		},
		getImg() {
			return this.imgs = true
		},
		uploadSizeError(value) {
			if (!value) {
				this.$refs.dropdownMenu.hidePopup();
			}
		},
		uploadUploaded(item, data) {
			const validImageTypes = ['image/gif', 'image/jpeg', 'image/png'];

			if (!validImageTypes.includes(data.file.type)) {

				this.$emit('anyFileSend', data)
				this.sendFile(data)

			} else {


				this.$emit('previewImg', data)

				this.imageWH(data).then(info => {
					return this.sendImage(data, info)
				}).catch(e => {
					console.log('e', e)
				})


			}
		},
		imageWH(file) {
			const img = new Image();
			var imgInfo = {};

			return new Promise((resolve, reject) => {

				img.onload = function () {
					imgInfo.w = this.width
					imgInfo.h = this.height
					resolve(imgInfo)
				}

				img.onerror = function (e) {
					reject(e)
				}

				img.src = file.base64
				//this.info = imgInfo

			})


		},
		uploadUploadedAll(item, result) {
			this.$store.state.loading = false
			this.$refs.dropdownMenu.hidePopup();
		}

	},
}
