import {mapState} from 'vuex';
import chatPreview from '@/components/chat/preview/index.vue'
import {Swiper, SwiperSlide} from 'vue-awesome-swiper'
import _ from 'underscore'
import f from "@/application/functions";

export default {
  name: 'chatJoin',
  props: {
    chat: Object,
    m_chat: {},
    usersinfo: Array,
    room: Object
  },

  components: {
    chatPreview,
    Swiper,
    SwiperSlide
  },

  data: function () {

    return {
      loading: false,
      joinedMembers: []
    }

  },

  created: () => {

  },

  watch: {
    //$route: 'getdata'
  },
  computed: mapState({
    auth: state => state.auth,
    pocketnet: state => state.pocketnet,
  }),
  mounted : function(){
 
  },

  methods: {
    join: function () {
      this.core.mtrx.client.joinRoom(this.m_chat.roomId).then(() => {

        this.$emit('joined')
      })
    },

    decline: function () {
      this.core.mtrx.client.leave(this.m_chat.roomId)
      this.$store.commit('DELETE_ROOM', this.chat.roomId);
      this.$router.go(-1)
    },
  },
}