import {mapState} from 'vuex';
import chatName from '@/components/chats/assets/name.vue'
import chatIcon from '@/components/chats/assets/icon.vue'
import userView from '@/components/user/view/pnuser/index.vue'
import {Swiper, SwiperSlide} from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'
//
import {Carousel, Slide} from 'vue-carousel';
import f from '@/application/functions.js'
import _ from "underscore";
//

export default {
  name: 'chatPreview',
  props: {
    chat: Object,
    usersinfo: Array,
    room: {}
  },

  components: {
    userView,
    chatName,
    chatIcon,
    Swiper,
    SwiperSlide,
    Carousel,
    Slide
  },

  data: function () {

    return {
      loading: false,
      usersinfoLocal: [],
      swiperOption: {
        direction: 'horizontal',
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      },
      convertedUsers: []
    }

  },

  computed: mapState({
    auth: state => state.auth,
    roomMembers: function () {

      var self = this
      var my = f.getmatrixid(this.core.mtrx.client.credentials.userId)
      var names = _.map(self.chat.members, function (member) {
        return member.name
      })

      this.core.user.usersInfo(names).then(r => {
        this.convertedUsers = _.map(r, function (user) {
          var data = {}
          _.map(self.chat.members, function (member) {
            if ((self.chat.members.length === 2) && user.id !== my && member.name === user.id) {
              return data = {
                id: user.id,
                name: user.name,
                image: user.image,
                status: member.status,
                source: user.source
              }
            }
            if((self.chat.members.length > 2) && member.name === user.id ){
              return data = {
                id: user.id,
                name: user.name,
                image: user.image,
                status: member.status,
                source: user.source
              }
            }

          })
          return data
        })
      }).catch(e => {
        console.log(e, "eeerror name")
      })
      return this.chat.members
    }
  }),
}